@charset "UTF-8";
.bbp-accordion-element details {
  border-color: var(--brand-bodycolour);
  border-bottom: solid 1px;
}
.bbp-accordion-element details summary {
  color: var(--brand-brandcolour);
  font-size: var(--brand-h3size);
  font-family: var(--brand-titlefamilyname);
  list-style: none;
  display: flex;
  align-items: center;
}
.bbp-accordion-element details summary:first-child {
  border-top: none;
}
.bbp-accordion-element details summary::before {
  content: "+";
  font-size: 1.4em;
  width: 3rem;
}
.bbp-accordion-element details[open] summary::before {
  content: "−";
}